import Button from "@material-ui/core/Button";
import Input from "@material-ui/core/Input";
import MenuItem from "@material-ui/core/MenuItem";
import Select from "@material-ui/core/Select";
import Typography from "@material-ui/core/Typography";
import React, { useEffect, useRef, useState } from "react";
import { useStoreOrdersToServer } from "../../../App/Common/DataSource/Context/StoreOrdersToServer";
import Link from "@material-ui/core/Link";
import DeleteIcon from "@material-ui/icons/Delete";
import IconButton from "@material-ui/core/IconButton";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import { Height } from "@material-ui/icons";

export const PreOrder = ({ vds, preOrders }) => {
  const [showSelectCountVDS, setShowSelectCountVDS] = useState(null);
  const [showInputQty, setShowInputQty] = useState(null);
  const [selectedValue, setSelectedValue] = useState(1);
  const [saved, setSaved] = useState();
  const storeOrdersToServer = useStoreOrdersToServer();
  const inputRef = useRef();
  const inputChange = () => {
    if (process.env.REACT_APP_DEBUG)
      console.log("inputChange", inputRef.current.value);
    if (inputRef.current.value > 99) {
      inputRef.current.value = 99;
    }
  };

  useEffect(() => {
    if (!preOrders) {
      return;
    }
    let finded = preOrders.find((element) => element._id === vds.id);
    if (process.env.REACT_APP_DEBUG) console.log("finded", finded);
    if (finded) {
      setSaved(finded);
    }
  }, [preOrders]);

  return (
    <>
      {saved && saved.qty > 0 ? (
        <Grid
          container
          direction="row"
          style={{ padding: 8, alignItems: "center" }}
        >
          <Grid item xs>
            <Typography component="div">
              <Box fontWeight="fontWeightBold" m={1}>
                Ordered: {saved.qty}
              </Box>
            </Typography>
          </Grid>
          <Grid item xs>
            <Grid container justify="flex-end" style={{ paddingRight: 4 }}>
              <IconButton
                size="small"
                onClick={() => {
                  storeOrdersToServer.store(vds.id, 0);
                  setSaved({ _id: vds.id, qty: 0 });
                }}
                style={{ width: 40, height: 40 }}
              >
                <DeleteIcon color="secondary" />
              </IconButton>
            </Grid>
          </Grid>
        </Grid>
      ) : (
        <>
          {showSelectCountVDS === vds.id ? (
            <Grid
              container
              direction="row"
              style={{ padding: 8, alignItems: "center" }}
            >
              <Grid item xs style={{ display: "flex" }}>
                <Typography component="div">
                  <Box fontWeight="fontWeightRegular" m={1}>
                    Qty:
                  </Box>
                </Typography>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={selectedValue}
                  onChange={(e) => setSelectedValue(e.target.value)}
                >
                  {[...Array(9).keys()].map((v) => (
                    <MenuItem value={v}>{v}</MenuItem>
                  ))}
                  <MenuItem
                    value={10}
                    onClick={() => {
                      setShowInputQty(vds.id);
                      setShowSelectCountVDS(null);
                    }}
                  >
                    10+
                  </MenuItem>
                </Select>
                <Button
                  style={{ marginLeft: 8 }}
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    storeOrdersToServer.store(vds.id, selectedValue);
                    setSaved({ _id: vds.id, qty: selectedValue });
                  }}
                >
                  SAVE
                </Button>
              </Grid>
            </Grid>
          ) : (
            <>
              {showInputQty === vds.id ? (
                <Grid
                  container
                  direction="row"
                  style={{ padding: 8, alignItems: "center" }}
                >
                  <Grid item xs style={{ display: "flex" }}>
                    {" "}
                    <Input
                      defaultValue={10}
                      style={{ width: 25, marginLeft: 8, marginRight: 8 }}
                      inputRef={inputRef}
                      onChange={() => inputChange()}
                    />
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={() => {
                        storeOrdersToServer.store(
                          vds.id,
                          Number(inputRef.current.value)
                        );
                        setSaved({
                          _id: vds.id,
                          qty: Number(inputRef.current.value),
                        });
                      }}
                    >
                      SAVE
                    </Button>
                  </Grid>
                </Grid>
              ) : (
                <Button
                  variant="contained"
                  color="primary"
                  style={{ margin: 10 }}
                  onClick={() => {
                    setShowSelectCountVDS(vds.id);
                  }}
                >
                  Pre-Order
                </Button>
              )}
            </>
          )}
        </>
      )}
    </>
  );
};
