import { Link } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import Input from "@material-ui/core/Input";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import React, { useEffect, useState } from "react";
const useStyles = makeStyles(() => ({
  root: {
    marginTop: 20,
  },
  gridContainer: {
    minWidth: 300,
  },
  item: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  input: {
    display: "inline-flex",
  },
}));

export const Server = (props) => {
  const classes = useStyles();

  const [ssd, setSSD] = useState(props.ssd);
  const [cpu, setCPU] = useState(props.cpu);
  const [ram, setRAM] = useState(props.ram);

  const [serversCount, setServersCount] = useState(8);

  useEffect(() => {
    setCPU(props.cpu);
  }, [props.cpu]);

  useEffect(() => {
    setRAM(props.ram);
  }, [props.ram]);

  useEffect(() => {
    setSSD(props.ssd);
  }, [props.ssd]);

  const handleChangeSSD = (e) => {
    setSSD(e.target.value);
    props.setSSD(Number(e.target.value), props.index);
  };
  const handleChangeCPU = (e) => {
    setCPU(e.target.value);
    props.setCPU(Number(e.target.value), props.index);
  };
  const handleChangeRAM = (e) => {
    setRAM(e.target.value);
    props.setRAM(Number(e.target.value), props.index);
  };

  const handleRemove = (id) => {
    props.handleRemove(props.index, id);
  };

  const handleChangeServersCount = (e) => {
    let count = Number(e.target.value);
    if (!count) {
      count = 1;
    }
    setServersCount(count);
    props.handleChangeServersCount(count, props.index);
  };

  return (
    <Grid container>
      <Grid item sm={4} xs={4} className={classes.item}>
        <div className={classes.input}>
          <Typography variant="h5">vCPUs</Typography>
          <Typography variant="body1" style={{ lineHeight: 2.4 }}>
            &nbsp;
          </Typography>
        </div>{" "}
        <div className={classes.input}>
          <div style={{ alignSelf: "center" }}>
            <OutlinedInput
              defaultValue="16"
              inputProps={{ "aria-label": "description" }}
              style={{ width: 95 }}
              onChange={(e) => handleChangeCPU(e)}
            />
          </div>
        </div>
        
      </Grid>
      <Grid item sm={4} xs={4} className={classes.item}>
        <div className={classes.input}>
          <Typography variant="h5">RAM</Typography>
          <Typography variant="body1" style={{ lineHeight: 2.4 }}>
            &nbsp;/GB
          </Typography>
        </div>
        <div className={classes.input}>
          <div style={{ alignSelf: "center" }}>
            <OutlinedInput
              type="text"
              defaultValue="64"
              style={{ width: 95 }}
              onChange={(e) => handleChangeRAM(e)}
            />
          </div>
        </div>
        
      </Grid>
      <Grid item sm={4} xs={4} className={classes.item}>
        <div className={classes.input}>
          <Typography variant="h5">SSD</Typography>
          <Typography variant="body1" style={{ lineHeight: 2.4 }}>
            &nbsp;/GB
          </Typography>
        </div>
        <div className={classes.input}>
          <div style={{ alignSelf: "center" }}>
            <OutlinedInput
              defaultValue="4000"
              inputProps={{ "aria-label": "description" }}
              style={{ width: 95 }}
              onChange={(e) => handleChangeSSD(e)}
            />
          </div>
        </div>
      </Grid>
      <Grid item sm={12} xs={12}>
        <hr></hr>
      </Grid>
      <Grid
        item
        sm={4}
        xs={4}
        className={classes.item}
        style={{ alignItems: "end" }}
      >
        <div className={classes.input}>
          <div style={{ lineHeight: 3, paddingRight: 10 }}>Quantity:</div>
          <div style={{ alignSelf: "center" }}>
            <Input
              defaultValue={serversCount}
              inputProps={{ "aria-label": "description" }}
              style={{ width: 25 }}
              onChange={(e) => handleChangeServersCount(e)}
              value={serversCount}
            />
          </div>
        </div>
      </Grid>
      <Grid item sm={8} xs={8}></Grid>
      <Grid
        item
        sm={4}
        xs={4}
        className={classes.item}
        style={{ alignItems: "end" }}
      >
        <Link
          onClick={() => handleRemove(props.id)}
          href="#"
          className={classes.margin}
        >
          Delete
        </Link>
      </Grid>
      <Grid item sm={8} xs={8}></Grid>
    </Grid>
  );
};
