import { Box } from "@material-ui/core";
import Collapse from "@material-ui/core/Collapse";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import Paper from "@material-ui/core/Paper";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Typography from "@material-ui/core/Typography";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp";
import React, { useEffect, useState } from "react";
import { TopAppBar } from "../App/Common/Components/TopAppBar";
import { useFetch } from "../App/Common/Hooks/useFetch";
import Button from "@material-ui/core/Button";
import { useHistory } from "react-router-dom";
import { client } from "../App/Common/DataSource/client";

const useStyles = makeStyles(() => ({
  root: {
    marginTop: 20,
  },
  gridContainer: {
    minWidth: 300,
  },
}));

const useRowStyles = makeStyles({
  root: {
    "& > *": {
      borderBottom: "unset",
    },
  },
});

function Row(props) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();

  if (process.env.REACT_APP_DEBUG) console.log("row", row);

  return (
    <React.Fragment>
      <TableRow className={classes.root}>
        <TableCell>
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => setOpen(!open)}
          >
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell align="left">{row.numServers}</TableCell>
        <TableCell align="left">{row.country}</TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box margin={1}>
              <Typography variant="h6" gutterBottom component="div">
                Servers
              </Typography>
              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell>Count</TableCell>
                    <TableCell>vCPUs</TableCell>
                    <TableCell>RAM</TableCell>
                    <TableCell>SSD</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {row.history.map((historyRow) => (
                    <TableRow>
                      <TableCell>{historyRow.count}</TableCell>
                      <TableCell>{historyRow.cpu}</TableCell>
                      <TableCell>{historyRow.ram}GB</TableCell>
                      <TableCell>{historyRow.ssd}GB</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}

export const HostList = () => {
  const classes = useStyles();
  const [data, loading] = useFetch("/vds.json");
  const history = useHistory();
  const [rows, setRows] = useState([]);
  useEffect(() => {
    client({
      method: "get",
      url: "/hosts",
    })
      .then((res) => {
        let rows_ = [];
        for (const [k, v] of Object.entries(res.data)) {
          if (process.env.REACT_APP_DEBUG) console.log("v", v);
          let numServers = 0;
          v.list.forEach((element) => {
            numServers += element.count;
          });
          rows_.push({
            id: k,
            numServers: numServers,
            country: v.country[0],
            history: v.list,
          });
        }
        if (process.env.REACT_APP_DEBUG) console.log("res.data", rows_);

        setRows(rows_);
      })
      .catch((error) => {
        if (process.env.REACT_APP_DEBUG) console.log("error", error);
      });
  }, []);
  if (loading) {
    return (
      <>
        <TopAppBar />
      </>
    );
  }

  if (process.env.REACT_APP_DEBUG) console.log("data", data);
  return (
    <>
      <TopAppBar />
      <Grid container className={classes.root} spacing={2}>
        <Grid item xs={12}>
          <Container maxWidth="md">
            <Button
              variant="contained"
              color="primary"
              style={{ marginBottom: 20 }}
              onClick={() => history.push("/host")}
            >
              My servers
            </Button>
            <TableContainer component={Paper}>
              <Table aria-label="collapsible table">
                <TableHead>
                  <TableRow>
                    <TableCell />
                    <TableCell align="left">Servers</TableCell>
                    <TableCell align="left">Country</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {rows.map((row) => (
                    <Row key={row.name} row={row} />
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Container>
        </Grid>
      </Grid>
    </>
  );
};
