import { Box, Link } from "@material-ui/core";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import { makeStyles } from "@material-ui/core/styles";
import Tab from "@material-ui/core/Tab";
import Tabs from "@material-ui/core/Tabs";
import Typography from "@material-ui/core/Typography";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/firestore";
import React, { useEffect, useState } from "react";
import { useFireBaseAuth } from "../../App/Common/Autorization/Context/FireBase";
import { BottomAppBar } from "../../App/Common/Components/BottomAppBar";
import { TopAppBar } from "../../App/Common/Components/TopAppBar";
import { useWindowWidth } from "../../App/Common/Context/WindowWidthContext";
import { client } from "../../App/Common/DataSource/client";
import { useFetch } from "../../App/Common/Hooks/useFetch";
import { VDS } from "./vds";

export const BuyerIndex = () => {
  const [data, loading] = useFetch("/vds.json");
  const [competitorsData, competitorsDataLoading] =
    useFetch("/competitors.json");
  const [preOrders, setPreOrders] = useState(null);
  const [plan, setPlan] = useState("N1");
  const fireBaseAuth = useFireBaseAuth();
  const windowWidth = useWindowWidth();
  const useStyles = makeStyles(() => ({
    root: {
      marginTop: 15,
    },
    gridContainer: {
      minWidth: 300,
    },
    tab: {
      "& .MuiTab-root": {
        minWidth: windowWidth.width > 1000 ? 160 : 80,
      },
    },
  }));
  const classes = useStyles(windowWidth);

  useEffect(() => {
    if (!fireBaseAuth.isSignIn) {
      return;
    }

    firebase
      .auth()
      .currentUser.getIdToken(true)
      .then((idToken) => {
        client({
          method: "get",
          url: "/pre-order",
          headers: {
            AuthToken: idToken,
          },
        })
          .then((res) => {
            if (process.env.REACT_APP_DEBUG)
              console.log("res.data.message", res.data);
            setPreOrders(res.data);
          })
          .catch((error) => {
            if (process.env.REACT_APP_DEBUG) console.log("error", error);
          });
      })
      .catch((error) => {
        if (process.env.REACT_APP_DEBUG) console.log("error", error);
      });
  }, [fireBaseAuth]);

  if (loading || competitorsDataLoading) {
    return (
      <>
        <TopAppBar />
      </>
    );
  }

  const handleChange = (e, selectedPlan) => {
    setPlan(selectedPlan);
  };

  competitorsData[plan]["names"].forEach((name, i) => {
    if (!name) {
      competitorsData[plan]["names"][i] = competitorsData["N1"]["names"][i];
    }
  });
  /* const tmpBlock = [1, 2, 3, 4, 6, 8];
  competitorsData[plan]["names"].forEach((name, i) => {
    if (i === 5) {
      competitorsData[plan]["names"][i] = "Amazon";
    }
    if (i === 7) {
      competitorsData[plan]["names"][i] = "Google";
    }
    if (i === 9) {
      competitorsData[plan]["names"][i] = "Microsoft";
    }
    if (tmpBlock.includes(i)) {
      delete competitorsData[plan]["names"][i];
      delete competitorsData[plan]["price_m"][i];
      delete competitorsData[plan]["price_h"][i];
      delete competitorsData[plan]["vcpu"][i];
      delete competitorsData[plan]["ram"][i];
      delete competitorsData[plan]["disk"][i];
    }
  });
 */
  competitorsData[plan]["links"].forEach((link, i) => {
    if (!link) {
      competitorsData[plan]["links"][i] = competitorsData["N1"]["links"][i];
    }
  });

  if (process.env.REACT_APP_DEBUG)
    console.log("competitorsData", competitorsData["N1"]["price_m"][0]);
  return (
    <>
      <TopAppBar />
      <Grid container className={classes.root} spacing={0}>
        <Grid item xs={12}>
          <Container maxWidth="md">
            <Box style={{ padding: 10 }}>
              <Typography variant="body1" gutterBottom>
                NeoHost offers the best prices on the market at an SLA of
                99.99%. We do this by renting computing power from users around
                the world. High availability is achieved through continuous
                backups. Servers, traffic and backups are encrypted.
              </Typography>
            </Box>
          </Container>
        </Grid>

        <Grid item xs={12} style={{ padding: 8 }}>
          <Typography variant="h4" gutterBottom style={{ textAlign: "center" }}>
            Dedicated vCPU Hosting Plans
          </Typography>
          <Grid container justify="center" spacing={2}>
            {data["dedicated"].map((vds) => (
              <VDS preOrders={preOrders} vds={vds} />
            ))}
          </Grid>{" "}
          <Typography
            variant="h6"
            gutterBottom
            style={{ textAlign: "center", marginTop: 10 }}
          >
            New pricing plans coming soon
          </Typography>
        </Grid>
        <Grid item xs={12} style={{ padding: 8, marginTop: 40 }}>
          <Typography variant="h4" gutterBottom style={{ textAlign: "center" }}>
            Neohost vs Competitors
          </Typography>
          <Grid
            container
            direction="column"
            justify="center"
            alignItems="center"
            spacing={2}
          >
            <Tabs
              aria-label="simple tabs example"
              value={plan}
              onChange={handleChange}
              className={classes.tab}
            >
              <Tab label="N1" value={"N1"} />
              <Tab label="N2" value={"N2"} />
              <Tab label="N3" value={"N3"} />
              <Tab label="N4" value={"N4"} />
            </Tabs>
            {windowWidth.width > 1000 ? (
              <Grid item style={{ minWidth: 800 }}>
                <Paper>
                  <Grid container spacing={0}>
                    <Grid item xs={12} sm container direction="column">
                      <Grid
                        item
                        container
                        direction="row"
                        style={{ padding: 12 }}
                      >
                        <Grid item xs>
                          <Typography variant="body2" gutterBottom></Typography>
                        </Grid>

                        {competitorsData[plan]["names"].map((name, i) => (
                          <Grid item xs style={{ width: 100, marginTop: 6 }}>
                            <Typography variant="body2">
                              {competitorsData[plan]["links"][i] ? (
                                <Link
                                  href={competitorsData[plan]["links"][i]}
                                  target="_blank"
                                >
                                  {name}
                                </Link>
                              ) : (
                                <Typography
                                  variant="body2"
                                  style={{
                                    fontWeight: "bold",
                                    color: "rgb(0,0,0)",
                                  }}
                                >
                                  {name}
                                </Typography>
                              )}
                            </Typography>
                          </Grid>
                        ))}
                      </Grid>

                      <hr
                        style={{ width: "100%", border: "1px solid #f0f0f0" }}
                      ></hr>
                      <Grid
                        item
                        container
                        direction="row"
                        style={{ padding: 12 }}
                      >
                        <Grid item xs>
                          <Typography variant="body2" gutterBottom>
                            Price / mo
                          </Typography>
                        </Grid>
                        {competitorsData[plan]["price_m"].map((price_m, i) => (
                          <Grid item xs>
                            <Typography
                              variant="body2"
                              gutterBottom
                              style={{
                                fontWeight: i == 0 ? "bold" : "normal",
                              }}
                            >
                              {price_m.length > 1 ? <>${price_m}</> : <>-</>}
                            </Typography>
                          </Grid>
                        ))}
                      </Grid>
                      <hr
                        style={{
                          width: "100%",
                          border: "1px solid #f0f0f0",
                          margin: 0,
                        }}
                      ></hr>
                      <Grid
                        item
                        container
                        direction="row"
                        style={{ padding: 12 }}
                      >
                        <Grid item xs>
                          <Typography variant="body2" gutterBottom>
                            Price / hr
                          </Typography>
                        </Grid>
                        {competitorsData[plan]["price_h"].map((price_h, i) => (
                          <Grid item xs>
                            <Typography
                              variant="body2"
                              gutterBottom
                              style={{
                                fontWeight: i == 0 ? "bold" : "normal",
                              }}
                            >
                              {price_h.length > 1 ? <>${price_h}</> : <>-</>}
                            </Typography>
                          </Grid>
                        ))}
                      </Grid>
                      <hr
                        style={{
                          width: "100%",
                          border: "1px solid #f0f0f0",
                          margin: 0,
                        }}
                      ></hr>
                      <Grid
                        item
                        container
                        direction="row"
                        style={{ padding: 12 }}
                      >
                        <Grid item xs>
                          <Typography variant="body2" gutterBottom>
                            vCPU
                          </Typography>
                        </Grid>
                        {competitorsData[plan]["vcpu"].map((vcpu, i) => (
                          <Grid item xs>
                            <Typography
                              variant="body2"
                              gutterBottom
                              style={{
                                fontWeight: i == 0 ? "bold" : "normal",
                              }}
                            >
                              {vcpu}
                            </Typography>
                          </Grid>
                        ))}
                      </Grid>
                      <hr
                        style={{
                          width: "100%",
                          border: "1px solid #f0f0f0",
                          margin: 0,
                        }}
                      ></hr>
                      <Grid
                        item
                        container
                        direction="row"
                        style={{ padding: 12 }}
                      >
                        <Grid item xs>
                          <Typography variant="body2" gutterBottom>
                            RAM / GB
                          </Typography>
                        </Grid>
                        {competitorsData[plan]["ram"].map((ram, i) => (
                          <Grid item xs>
                            <Typography
                              variant="body2"
                              gutterBottom
                              style={{
                                fontWeight: i == 0 ? "bold" : "normal",
                              }}
                            >
                              {ram}
                            </Typography>
                          </Grid>
                        ))}
                      </Grid>
                      <hr
                        style={{
                          width: "100%",
                          border: "1px solid #f0f0f0",
                          margin: 0,
                        }}
                      ></hr>
                      <Grid
                        item
                        container
                        direction="row"
                        style={{ padding: 12 }}
                      >
                        <Grid item xs>
                          <Typography variant="body2" gutterBottom>
                            Disk / GB
                          </Typography>
                        </Grid>
                        {competitorsData[plan]["disk"].map((disk, i) => (
                          <Grid item xs>
                            <Typography
                              variant="body2"
                              gutterBottom
                              style={{
                                fontWeight: i == 0 ? "bold" : "normal",
                              }}
                            >
                              {disk}
                            </Typography>
                          </Grid>
                        ))}{" "}
                      </Grid>
                      <hr
                        style={{
                          width: "100%",
                          border: "1px solid #f0f0f0",
                          margin: 0,
                        }}
                      ></hr>
                      <Grid item xs container direction="column">
                        <Grid
                          item
                          xs
                          direction="row"
                          style={{ height: 56, display: "flex" }}
                        ></Grid>
                      </Grid>
                    </Grid>
                  </Grid>
                </Paper>
              </Grid>
            ) : (
              <Grid item style={{ minWidth: 350 }}>
                <Paper>
                  <Grid container spacing={0}>
                    <Grid item xs={12} sm container direction="column">
                      <Grid item xs container direction="column" spacing={0}>
                        {competitorsData[plan]["names"].map((name, i) => (
                          <>
                            {competitorsData[plan]["price_m"][i].length > 1 ? (
                              <>
                                <Grid
                                  xs
                                  container
                                  direction="column"
                                  style={{ padding: 12 }}
                                >
                                  <Grid item xs container direction="row">
                                    <Typography
                                      variant="body2"
                                      style={{
                                        paddingLeft: 8,
                                        fontSize: 16,
                                        fontWeight: "bold",
                                        color: "rgb(0, 123, 252)",
                                      }}
                                    >
                                      ${competitorsData[plan]["price_m"][i]}
                                      /mo
                                    </Typography>
                                    {competitorsData[plan]["price_h"][i]
                                      .length > 1 && (
                                      <Typography
                                        variant="body2"
                                        style={{
                                          paddingLeft: 14,
                                          fontSize: 16,
                                          color: "rgb(161, 165, 178)",
                                        }}
                                      >
                                        ${competitorsData[plan]["price_h"][i]}
                                        /hr
                                      </Typography>
                                    )}
                                  </Grid>
                                  <Grid xs item direction="column">
                                    <Typography
                                      variant="body2"
                                      style={{
                                        paddingLeft: 8,
                                        fontSize: 16,
                                        color: "rgb(0, 0, 0)",
                                      }}
                                    >
                                      {competitorsData[plan]["vcpu"][i]} vCPU /{" "}
                                      {competitorsData[plan]["ram"][i]} RAM /{" "}
                                      {competitorsData[plan]["disk"][i]}{" "}
                                    </Typography>
                                    <Typography
                                      variant="body2"
                                      style={{
                                        paddingLeft: 8,
                                        fontSize: 16,
                                        color: "rgb(161, 165, 178)",
                                      }}
                                    >
                                      {competitorsData[plan]["links"][i] ? (
                                        <Link
                                          href={
                                            competitorsData[plan]["links"][i]
                                          }
                                          target="_blank"
                                        >
                                          {name}
                                        </Link>
                                      ) : (
                                        <Typography
                                          variant="body2"
                                          style={{
                                            fontSize: 16,
                                            fontWeight: "bold",
                                            color: "rgb(0,0,0)",
                                          }}
                                        >
                                          {name}
                                        </Typography>
                                      )}
                                    </Typography>
                                  </Grid>
                                </Grid>
                                <hr
                                  style={{
                                    width: "100%",
                                    border: "1px solid #f0f0f0",
                                    margin: 0,
                                  }}
                                ></hr>
                              </>
                            ) : (
                              <></>
                            )}
                          </>
                        ))}
                      </Grid>
                    </Grid>
                  </Grid>
                </Paper>
              </Grid>
            )}
          </Grid>
        </Grid>
      </Grid>
      <BottomAppBar />
    </>
  );
};
