import AppBar from "@material-ui/core/AppBar";
import Backdrop from "@material-ui/core/Backdrop";
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import Link from "@material-ui/core/Link";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import OutlinedInput from "@material-ui/core/OutlinedInput";
import { makeStyles } from "@material-ui/core/styles";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import AccountCircle from "@material-ui/icons/AccountCircle";
import firebase from "firebase/app";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import logoGithub from "super-tiny-icons/images/svg/github.svg";
import { useFireBaseAuth } from "../Autorization/Context/FireBase";
import { useLocalForage } from "../Context/localForageContext";
import logoGmail from "super-tiny-icons/images/svg/google.svg";
import { client } from "../DataSource/client";
import { useWindowWidth } from "../Context/WindowWidthContext";
import Box from "@material-ui/core/Box";

function deleteAllCookies() {
  var cookies = document.cookie.split(";");

  for (var i = 0; i < cookies.length; i++) {
    var cookie = cookies[i];
    var eqPos = cookie.indexOf("=");
    var name = eqPos > -1 ? cookie.substr(0, eqPos) : cookie;
    document.cookie = name + "=;expires=Thu, 01 Jan 1970 00:00:00 GMT";
  }
}
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    backgroundColor: "white",
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  logo: {
    marginRight: 10,
  },
}));

export const TopAppBar = () => {
  const classes = useStyles();
  const [auth, setAuth] = useState(true);
  const [showInviteScreen, setShowInviteScreen] = useState(false);
  const [showSignInScreen, setShowSignInScreen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [stat, setStat] = useState(0);
  const open = Boolean(anchorEl);
  const history = useHistory();
  const fireBaseAuth = useFireBaseAuth();
  const windowWidth = useWindowWidth();

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const store = useLocalForage();

  useEffect(() => {
    if (!store) return;
    if (!fireBaseAuth) return;

    if (process.env.REACT_APP_DEBUG) console.log("fireBaseAuth", fireBaseAuth);

    if (fireBaseAuth.isSignIn) {
      setShowSignInScreen(false);
    } else if (!fireBaseAuth.loading && !fireBaseAuth.isSignIn) {
      setShowSignInScreen(true);
    }

    store.settings.getItem("invite").then(function (results) {
      if (process.env.REACT_APP_DEBUG) console.log("results", results);
      if (!results) {
        setShowInviteScreen(true);
        setShowSignInScreen(false);
      }
    });
  }, [store, fireBaseAuth]);

  const handleInvite = (e) => {
    if (process.env.REACT_APP_DEBUG) console.log("e", e.target.value);
    if (e.target.value === "2022") {
      setShowInviteScreen(false);
      setShowSignInScreen(true);
      store.settings.setItem("invite", "2022");
    }
  };
  useEffect(() => {
    client({
      method: "get",
      url: "/stat",
    })
      .then((res) => {
        setStat(res.data);
        if (process.env.REACT_APP_DEBUG)
          console.log("res.data.message", res.data);
      })
      .catch((error) => {
        if (process.env.REACT_APP_DEBUG) console.log("error", error);
      });
  }, []);

  return (
    <div className={classes.root}>
      <AppBar position="static" style={{ backgroundColor: "white" }}>
        <Toolbar variant="dense" style={{ paddingRight: 4, paddingLeft: 14 }}>
          <img
            src="/logo2.png"
            className={classes.logo}
            onClick={() => history.push("/")}
            style={{
              maxWidth: windowWidth.width > 400 ? 110 : 85,
              marginRight: windowWidth.width > 400 ? 15 : 10,
            }}
          />

          <Grid container>
            <Grid item xs={12}>
              <Typography
                variant="body2"
                className={classes.title}
                color="textPrimary"
                style={{ lineHeight: 1.3 }}
              >
                {stat.preOrdered} servers pre-ordered
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Typography
                variant="body2"
                className={classes.title}
                color="textPrimary"
                style={{ lineHeight: 1.3 }}
              >
                <Link href="/hostList">{stat.hosts} hosts added</Link>
              </Typography>
            </Grid>
          </Grid>

          {auth && (
            <div>
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleMenu}
                color="black"
              >
                <AccountCircle />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                open={open}
                onClose={handleClose}
              >
                <MenuItem
                  onClick={() => {
                    handleClose();
                    history.push("/host");
                  }}
                >
                  My servers
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    handleClose();
                    setTimeout(() => {
                      try {
                        firebase.auth().signOut();
                      } catch (err) {
                        alert("Error3... Please try again.");
                      }
                      deleteAllCookies();
                    }, 50);
                  }}
                >
                  Logout
                </MenuItem>
              </Menu>
            </div>
          )}
        </Toolbar>
      </AppBar>

      <Backdrop className={classes.backdrop} open={showInviteScreen}>
        <OutlinedInput
          placeholder={"Invite"}
          style={{ width: 110 }}
          onChange={(e) => handleInvite(e)}
        />
      </Backdrop>
      <Backdrop className={classes.backdrop} open={showSignInScreen}>
        <Grid container style={{ maxWidth: 500, justifyContent: "center" }}>
          <Grid item xs={12} sm={6}>
              <ListItem
                style={{ height: 48, justifyContent:"center" }}
                button
                key="GithubAuthProvider"
                onClick={() => {
                  setTimeout(() => {
                    try {
                      const GithubAuthProvider =
                        new firebase.auth.GithubAuthProvider();
                      firebase.auth().signInWithPopup(GithubAuthProvider);
                    } catch (err) {
                      alert("Error5... Please try again.");
                    }
                  }, 50);
                }}
              >
                <ListItemIcon style={{ minWidth: 35 }}>
                  <img src={logoGithub} style={{ width: 20, margin: 3 }} />
                </ListItemIcon>
                <div id="drawer-sign-in-with-github">
                  <ListItemText primary="Sign In with Github" />
                </div>
              </ListItem>
          </Grid>
          <Grid item xs={12} sm={6}>
              <ListItem
                style={{ height: 48, justifyContent:"center" }}
                button
                key="GoogleAuthProvider"
                onClick={() => {
                  setTimeout(() => {
                    try {
                      const GoogleAuthProvider =
                        new firebase.auth.GoogleAuthProvider();
                      firebase.auth().signInWithPopup(GoogleAuthProvider);
                    } catch (err) {
                      alert("Error5... Please try again.");
                    }
                  }, 50);
                }}
              >
                <ListItemIcon style={{ minWidth: 35 }}>
                  <img src={logoGmail} style={{ width: 20, margin: 3 }} />
                </ListItemIcon>
                <div id="drawer-sign-in-with-google">
                  <ListItemText primary="Sign In with Google" />
                </div>
              </ListItem>
          </Grid>
         {/*  <Grid item>14 people have signed up</Grid> */}
        </Grid>
      </Backdrop>
    </div>
  );
};
