import firebase from "firebase/app";
import "firebase/auth";
import { useLayoutEffect } from "react";
import { useFireBaseAuth } from "./App/Common/Autorization/Context/FireBase";
import { config } from "./firebase.config";

export default function InitUpdate() {
  const fireBaseAuth = useFireBaseAuth();

  if (!firebase.apps.length) {
    firebase.initializeApp({ ...config });
  }

  useLayoutEffect(() => {
    if (process.env.REACT_APP_DEBUG) console.log("InitUpdate useLayoutEffect");

    try {
      firebase.auth().onAuthStateChanged(function (user) {
        if (process.env.REACT_APP_DEBUG) console.log("onAuthStateChanged");

        if (user && user.providerData && user.providerData[0]) {
          if (process.env.REACT_APP_DEBUG) console.log("fireBaseAuth.signIn");
          fireBaseAuth.signIn(user.providerData[0], user.uid);
        } else {
          fireBaseAuth.signOut();
        }
      });
    } catch (err) {
      alert("Error1... Please try again.");
    }
  }, []);

  return <></>;
}
