import React, { useContext, useState } from "react";

const StoreOrdersToServerContext = React.createContext();

export const useStoreOrdersToServer = () => {
  return useContext(StoreOrdersToServerContext);
};

export const StoreOrdersToServerProvider = ({ children }) => {
  const [id, setId] = useState(0);
  const [qty, setQty] = useState(0);

  const store = (id, qty) => {
    if (process.env.REACT_APP_DEBUG) console.log("id, qty", id, qty);
    setId(Number(id));
    setQty(Number(qty));
  };

  return (
    <StoreOrdersToServerContext.Provider value={{ store, id, qty }}>
      {children}
    </StoreOrdersToServerContext.Provider>
  );
};
